.seccion{
padding: 1.5rem;

    .h2-t2{
        color: $blanco;
        margin-bottom: 1rem;
    }

    .gallery{
        // display: inline-block;
        display: flex;
        gap: 1rem;
        overflow: auto;

        &::-webkit-scrollbar {
            height: .5rem;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background-color: $color2;
        }
        &::-webkit-scrollbar-thumb:hover {
            background-color: $color1;
        }

        img{
            height: 17rem;
            border-radius: .3rem;
            overflow: hidden;
        }
    }
}
