.phone-bg{
    display: none;

    @include mid{
        display: block;
        position:absolute;
        top: -8.1rem;
        left: -2px; 
        right: 0; 
        margin-left: auto; 
        margin-right: auto; 
        width: 505px;
    }
}
.wrapper{
    margin: 0 auto;
    width: 100vw;
    
    @include mid{
        margin: 10rem auto 5rem auto;
    }
    
    @include tablet{
        margin: 10rem auto 5rem auto;
    }
}
header{
    position: sticky;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    z-index: 10;
}
main{
    .main-contenido{
        position: relative;
        overflow: hidden;
    }
    &::-webkit-scrollbar {
        width: 1rem;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $color2;
    }
    &::-webkit-scrollbar-thumb:hover {
        background-color: $blanco   ;
    }
    
    @include mid{
        height: 55rem;
        overflow: auto;
    }
}