.bg-video{
    position: absolute;
    z-index: 2;
    max-height: 64.7rem;
    // max-width: 37.9rem;
    left: -100%;
    object-fit: cover;
}
.bg-img{
    // display: none;
    position: absolute;
    z-index: 3;
    height: 64.7rem;
    // height: 100%;
    width: 100%;
    background-image: url(/build/img/bg.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
#s1-index{
    // transition: 1s ease-in-out;
    position: relative;
    z-index: 4;
    margin-bottom: 3.5rem;
}



.s1-parte1{
    background: rgb(255,255,255);
    background: linear-gradient(0deg, rgba(255,255,255,0) 0%, rgba(0,0,0,1) 92%);
    padding-bottom: 10rem;

    .logo-cont{
        margin: 2rem;
        display: inline-block;

        .logo-ico{
            height: 3rem;
        }
    }
    .secciones-cont{
        display: flex;
        width: 100%;
        justify-content: space-evenly;

        .sec-boton{
            display: block;
            text-decoration: none;
            color: $blanco;
            font-size: 1.2rem;
            &:hover{
                text-shadow: $blanco .5px 0 10px;
            }
        }
    }
}
.s1-parte2{
    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(0,0,0,1) 96%);
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 30rem;
    
    .caract{
        img{
            width: 65% !important;
        }
    }
    .desc{
        display: flex;
        gap:.4rem;
        justify-content: center;
        padding:1rem 0
    }
}
.p-t1{
    color: $blanco;

    &:before{
        content:"● ";
    }
    &:first-child:before{
        content:"";
    }
}
.opciones{
    display: flex;
    justify-content: space-around;
}
.btn-t1{
    padding: 1rem;
    color: $blanco;
    text-decoration: none;
    display: grid;
    gap: .5rem;
    &>*{
        margin: 0 auto;
    }
}
.btn-t2{
    justify-content: center;
    text-decoration: none;
    padding: 1rem 2rem;
    background-color: $blanco;
    color: $negro;
    display: flex;
    height: fit-content;
    gap: .5rem;
    border-radius: .5rem;
    margin: auto 0;
    align-items: center;

    img{
        filter: $f_blanco;
    }
}