html{
    background-color:  $color1;
    font-size: 62.5%;
    box-sizing: border-box;
    font-family: 'avenir';
}
*,*:before,*:after{
    box-sizing: inherit;
    margin: 0;
    scroll-behavior: smooth;
}
body{
    font-family: avenir;
    z-index: 1;
    color: $blanco;
    position: relative;
}
.wrapper {
    max-width: 42rem;
    // width:95%;
    margin:0 auto;
    background-color: $negro;
    position: relative;
    
    @include mid{
        max-width: 38rem;
    }
}

// fuentes
.h1-t1{
    font: 900 3rem / 4rem $fuenteBase;
}
.h2-t1{
    font: 1.4rem / 2rem $fuenteBase;
}
.h2-t2{
    font: bold 2rem / 3rem $fuenteBase;
}
.h2-t3{
    font: bold 1.5rem / 2rem $fuenteBase;
}
.p-t1{
    font: 1.2rem / 1.6rem $fuenteBase;
}
.p-t2{
    font: bold 1.5rem / 2rem $fuenteBase;
}
.p-t3{
    font: 1.5rem / 2.3rem $fuenteBase;
}
.p-t4{
    font: 1.15rem / 1.6rem $fuenteBase;
}
.caja{
    font: bold 1.5rem / 2rem $fuenteBase;
}

// generales
.d-none{
    display: none;
}
.w-100{
    width: 100%;
}
.scale-1{
    transition: .5s ease;

    &:hover{
        transform: scale(1.1, 1.1);
    }
}