#s2-desc{
    padding: 2rem 2rem 0 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    
    .propiedades{
        display: flex;
        justify-content: space-between;
        align-items:center;
        
        .h2-t3{
            color: $color3;
        }
    }
}
.bloque{
    display: flex;
    align-items:center;
    gap: 1.5rem;

    .h2-t3{
        color: $gris3 !important;
    }

    .caja{
        padding: .2rem .4rem;
        border-radius: .2rem;
    }
    .t-categ{
        background-color: $gris4;
    }

    .t-calidad{
        border:solid .1rem $gris4;
    }
}
.botones{
    display:flex;
    flex-direction:column;
    gap: 1rem;
    .btn-t2, .btn-t3{
        border-radius: .3rem;
        padding: .5rem 2rem;
    }

    .btn-t3{
        justify-content: center;
        text-decoration: none;
        background-color: $gris4;
        color: $blanco;
        display: flex;
        height: fit-content;
        gap: .5rem;
        margin: auto 0;
        align-items: center;
    }
}
.desc{
    // margin-top:1rem;

    .p-t3{
        margin: 1rem 0;
        color: $gris2;
    }


}
.desc-caract{
    display: flex;
    flex-direction: column;
    gap: .5rem;
    padding: 2rem 0 1rem;
    color: $gris4;

    span{
        color: $gris3;
    }
}
.acciones{
    display: flex;

    .btn-t1{
        color: $gris4;

        &:hover{
            color: $blanco;
        }
    }
}
.separador{
    height:.4rem;
    width: 35%;
    border-radius: .5rem;
    background-color: $color2;
    margin-bottom: .5rem;
}