.header-contenido{
    background-color: $negro;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding: 2rem .7rem 1.5rem .7rem;
    border-top: solid .1px $gris5;

    .header-section{
        display: flex;
        flex-direction: column;
        text-decoration: none;
        align-items: center;

        img{
            width: 2.5rem;
            filter: $f_gris3;
        }
        .h2-t1{
            color: $gris3;
        }
        &:hover > .h-ico{
            filter: $f_negro;
        }
        &:hover > .h2-t1{
            color: $blanco;
        }
    }
}
