@charset "UTF-8";
@font-face {
  src: url(/build/font/Avenir.ttc);
  font-family: avenir;
}
html {
  background-color: #e75050;
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "avenir";
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  font-family: avenir;
  z-index: 1;
  color: #fff;
  position: relative;
}

.wrapper {
  max-width: 42rem;
  margin: 0 auto;
  background-color: #000;
  position: relative;
}
@media (min-width: 480px) {
  .wrapper {
    max-width: 38rem;
  }
}

.h1-t1 {
  font: 900 3rem/4rem avenir;
}

.h2-t1 {
  font: 1.4rem/2rem avenir;
}

.h2-t2 {
  font: bold 2rem/3rem avenir;
}

.h2-t3 {
  font: bold 1.5rem/2rem avenir;
}

.p-t1 {
  font: 1.2rem/1.6rem avenir;
}

.p-t2 {
  font: bold 1.5rem/2rem avenir;
}

.p-t3 {
  font: 1.5rem/2.3rem avenir;
}

.p-t4 {
  font: 1.15rem/1.6rem avenir;
}

.caja {
  font: bold 1.5rem/2rem avenir;
}

.d-none {
  display: none;
}

.w-100 {
  width: 100%;
}

.scale-1 {
  transition: 0.5s ease;
}
.scale-1:hover {
  transform: scale(1.1, 1.1);
}

.phone-bg {
  display: none;
}
@media (min-width: 480px) {
  .phone-bg {
    display: block;
    position: absolute;
    top: -8.1rem;
    left: -2px;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    width: 505px;
  }
}

.wrapper {
  margin: 0 auto;
  width: 100vw;
}
@media (min-width: 480px) {
  .wrapper {
    margin: 10rem auto 5rem auto;
  }
}
@media (min-width: 768px) {
  .wrapper {
    margin: 10rem auto 5rem auto;
  }
}

header {
  position: sticky;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  z-index: 10;
}

main .main-contenido {
  position: relative;
  overflow: hidden;
}
main::-webkit-scrollbar {
  width: 1rem;
}
main::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #E50914;
}
main::-webkit-scrollbar-thumb:hover {
  background-color: #fff;
}
@media (min-width: 480px) {
  main {
    height: 55rem;
    overflow: auto;
  }
}

.header-contenido {
  background-color: #000;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding: 2rem 0.7rem 1.5rem 0.7rem;
  border-top: solid 0.1px #3E3E3E;
}
.header-contenido .header-section {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  align-items: center;
}
.header-contenido .header-section img {
  width: 2.5rem;
  filter: invert(54%) sepia(1%) saturate(1609%) hue-rotate(343deg) brightness(96%) contrast(92%);
}
.header-contenido .header-section .h2-t1 {
  color: #838383;
}
.header-contenido .header-section:hover > .h-ico {
  filter: invert(0%) sepia(100%) saturate(40%) hue-rotate(95deg) brightness(91%) contrast(100%);
}
.header-contenido .header-section:hover > .h2-t1 {
  color: #fff;
}

.seccion {
  padding: 1.5rem;
}
.seccion .h2-t2 {
  color: #fff;
  margin-bottom: 1rem;
}
.seccion .gallery {
  display: flex;
  gap: 1rem;
  overflow: auto;
}
.seccion .gallery::-webkit-scrollbar {
  height: 0.5rem;
}
.seccion .gallery::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #E50914;
}
.seccion .gallery::-webkit-scrollbar-thumb:hover {
  background-color: #e75050;
}
.seccion .gallery img {
  height: 17rem;
  border-radius: 0.3rem;
  overflow: hidden;
}

.bg-video {
  position: absolute;
  z-index: 2;
  max-height: 64.7rem;
  left: -100%;
  object-fit: cover;
}

.bg-img {
  position: absolute;
  z-index: 3;
  height: 64.7rem;
  width: 100%;
  background-image: url(/build/img/bg.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

#s1-index {
  position: relative;
  z-index: 4;
  margin-bottom: 3.5rem;
}

.s1-parte1 {
  background: white;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0) 0%, black 92%);
  padding-bottom: 10rem;
}
.s1-parte1 .logo-cont {
  margin: 2rem;
  display: inline-block;
}
.s1-parte1 .logo-cont .logo-ico {
  height: 3rem;
}
.s1-parte1 .secciones-cont {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}
.s1-parte1 .secciones-cont .sec-boton {
  display: block;
  text-decoration: none;
  color: #fff;
  font-size: 1.2rem;
}
.s1-parte1 .secciones-cont .sec-boton:hover {
  text-shadow: #fff 0.5px 0 10px;
}

.s1-parte2 {
  background: white;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, black 96%);
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-top: 30rem;
}
.s1-parte2 .caract img {
  width: 65% !important;
}
.s1-parte2 .desc {
  display: flex;
  gap: 0.4rem;
  justify-content: center;
  padding: 1rem 0;
}

.p-t1 {
  color: #fff;
}
.p-t1:before {
  content: "● ";
}
.p-t1:first-child:before {
  content: "";
}

.opciones {
  display: flex;
  justify-content: space-around;
}

.btn-t1 {
  padding: 1rem;
  color: #fff;
  text-decoration: none;
  display: grid;
  gap: 0.5rem;
}
.btn-t1 > * {
  margin: 0 auto;
}

.btn-t2 {
  justify-content: center;
  text-decoration: none;
  padding: 1rem 2rem;
  background-color: #fff;
  color: #000;
  display: flex;
  height: fit-content;
  gap: 0.5rem;
  border-radius: 0.5rem;
  margin: auto 0;
  align-items: center;
}
.btn-t2 img {
  filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(320deg) brightness(103%) contrast(101%);
}

#s1-desc {
  display: flex;
  flex-direction: column;
}
#s1-desc .enlace-cont a {
  display: inline-block;
  padding: 1rem;
}

#s2-desc {
  padding: 2rem 2rem 0 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
#s2-desc .propiedades {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#s2-desc .propiedades .h2-t3 {
  color: #80d865;
}

.bloque {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.bloque .h2-t3 {
  color: #838383 !important;
}
.bloque .caja {
  padding: 0.2rem 0.4rem;
  border-radius: 0.2rem;
}
.bloque .t-categ {
  background-color: #555555;
}
.bloque .t-calidad {
  border: solid 0.1rem #555555;
}

.botones {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.botones .btn-t2, .botones .btn-t3 {
  border-radius: 0.3rem;
  padding: 0.5rem 2rem;
}
.botones .btn-t3 {
  justify-content: center;
  text-decoration: none;
  background-color: #555555;
  color: #fff;
  display: flex;
  height: fit-content;
  gap: 0.5rem;
  margin: auto 0;
  align-items: center;
}

.desc .p-t3 {
  margin: 1rem 0;
  color: #ACACAC;
}

.desc-caract {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 2rem 0 1rem;
  color: #555555;
}
.desc-caract span {
  color: #838383;
}

.acciones {
  display: flex;
}
.acciones .btn-t1 {
  color: #555555;
}
.acciones .btn-t1:hover {
  color: #fff;
}

.separador {
  height: 0.4rem;
  width: 35%;
  border-radius: 0.5rem;
  background-color: #E50914;
  margin-bottom: 0.5rem;
}